import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'index',
    redirect: 'home',
    component: () => import( /* webpackChunkName: "Index" */ '@/views/Index.vue'),
    children: [{
        path: 'home',
        name: 'home',
        component: () => import( /* webpackChunkName: "Home" */ '@/views/Home.vue'),
      },
      {
        path: 'download',
        name: 'download',
        component: () => import( /* webpackChunkName: "Download" */ '@/views/Download.vue'),
      },
      {
        path: 'conact',
        name: 'conact',
        component: () => import( /* webpackChunkName: "Conact" */ '@/views/Conact.vue'),
      },
      {
        path: '/about',
        name: 'about',
        component: () => import( /* webpackChunkName: "About" */ '@/views/About.vue'),
      },
      {
        path: '/show',
        name: 'show',
        component: () => import( /* webpackChunkName: "show" */ '@/views/Show.vue'),
      },
      {
        path: '/news',
        name: 'news',
        component: () => import( /* webpackChunkName: "Home" */ '@/views/News.vue'),
      },
      {
        path: '/plays',
        name: 'plays',
        component: () => import( /* webpackChunkName: "Plays" */ '@/views/Plays.vue'),
      },
      {
        path: '/answer',
        name: 'answer',
        component: () => import( /* webpackChunkName: "Answer" */ '@/views/Answer.vue'),
      },
      {
        path: '/detail',
        name: 'detail',
        component: () => import( /* webpackChunkName: "Detail" */ '@/views/Detail.vue'),
      },
    ]
  }]
})



